module.exports = {
  get: get,
  batch: batch,
  put: put,
  del: del,
  ready: ready,
  dbExists: dbExists
};

function get(db, path) {
  return new Promise(function (resolve, reject) {
    db.get(path, function (err, node) {
      if (err) return reject(err);
      return resolve(node);
    });
  });
}

function batch(db, batch) {
  return new Promise(function (resolve, reject) {
    db.batch(batch, function (err) {
      if (err) return reject(err);
      return resolve();
    });
  });
}

function put(db, path, obj) {
  return new Promise(function (resolve, reject) {
    db.put(path, obj, function (err) {
      if (err) return reject(err);
      return resolve();
    });
  });
}

function del(db, path) {
  return new Promise(function (resolve, reject) {
    db.del(path, function (err, node) {
      if (err) return reject(err);
      return resolve(node);
    });
  });
}

function ready(db) {
  return new Promise(function (resolve, reject) {
    db.on('ready', function () {
      resolve();
    });
  });
}

function dbExists(dbName) {
  return new Promise(function (resolve, reject) {
    var req = window.indexedDB.open(dbName);
    var existed = true;

    req.onsuccess = function () {
      req.result.close();

      if (!existed) {
        window.indexedDB.deleteDatabase(dbName);
      }

      resolve(existed);
    };

    req.onupgradeneeded = function () {
      existed = false;
    };

    req.onerror = function (err) {
      reject(err);
    };
  });
}